'use client';

import { useEffect, useState } from 'react';
import { FaBars, FaTimes } from 'react-icons/fa';
import Image from 'next/image';
import Link from 'next/link';
import { motion, AnimatePresence } from 'framer-motion';

export default function Navbar() {
  const [activeSection, setActiveSection] = useState<string | null>(null);
  const [menuOpen, setMenuOpen] = useState(false);
  const [language, setLanguage] = useState('English');

  useEffect(() => {
    document.documentElement.style.scrollBehavior = 'smooth';
    return () => {
      document.documentElement.style.scrollBehavior = 'auto';
    };
  }, []);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setActiveSection(entry.target.id);
          }
        });
      },
      { threshold: 0.4 }
    );

    const ids = ['home', 'about', 'service', 'web', 'marketing', 'contact'];
    ids.forEach((id) => {
      const section = document.getElementById(id);
      if (section) observer.observe(section);
    });

    return () => observer.disconnect();
  }, []);

  return (
    <motion.header
      initial={{ y: -80, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.5, ease: 'easeOut' }}
      className="fixed top-0 left-0 right-0 z-50 bg-white/70 bg-opacity-90 transition-all duration-300 font-serif border-2 border-white m-3 md:rounded-full rounded-2xl"
    >
      <div className="flex justify-between items-center px-4 py-3 sm:px-8">
        {/* Logo */}
        <motion.div
          initial={{ scale: 0.8, opacity: 0 }}
          animate={{ scale: 1, opacity: 1 }}
          transition={{ duration: 0.5 }}
        >
          <Image
            src="/logo.png"
            alt="Logo"
            width={80}
            height={80}
            className="object-contain transition-transform duration-500 hover:scale-110"
            priority
          />
        </motion.div>

        {/* Desktop Menu */}
        <nav className="hidden sm:flex gap-6 items-center">
          <a
            href="#home"
            className={`uppercase text-base font-semibold px-3 py-1 rounded-2xl transition-all ${
              activeSection === 'home'
                ? 'bg-orange-500 text-white shadow-md'
                : 'text-black hover:bg-orange-700 hover:text-white hover:shadow-md'
            }`}
          >
            Home
          </a>
          <a
            href="#about"
            className={`uppercase text-base font-semibold px-3 py-1 rounded-2xl transition-all ${
              activeSection === 'about'
                ? 'bg-orange-500 text-white shadow-md'
                : 'text-black hover:bg-orange-700 hover:text-white hover:shadow-md'
            }`}
          >
            About
          </a>

          {/* Service Dropdown */}
          <div className="relative group">
            <a
              href="#service"
              className={`uppercase text-base font-semibold px-3 py-1 rounded-2xl transition-all ${
                activeSection === 'service'
                  ? 'bg-orange-500 text-white shadow-md'
                  : 'text-black hover:bg-orange-700 hover:text-white hover:shadow-md'
              }`}
            >
              Service
            </a>
            <div className="absolute w-50 left-0 mt-2 hidden text-center group-hover:block bg-white border rounded-md shadow-md gap-2">
              <a
                href="#web"
                className="block px-4 py-2 text-base text-black hover:bg-orange-700 hover:text-white rounded-full"
              >
                Web Development
              </a>
              <a
                href="#marketing"
                className="block px-4 py-2 text-base text-black hover:bg-orange-700 hover:text-white rounded-full"
              >
                Digital Marketing
              </a>
            </div>
          </div>

          <Link
            href="./components/product"
            className="uppercase text-base font-semibold px-3 py-1 rounded-2xl text-black hover:bg-orange-700 hover:text-white hover:shadow-md"
          >
            Product
          </Link>
          <a
            href="#contact"
            className={`uppercase text-base font-semibold px-3 py-1 rounded-2xl transition-all ${
              activeSection === 'contact'
                ? 'bg-orange-500 text-white shadow-md'
                : 'text-black hover:bg-orange-700 hover:text-white hover:shadow-md'
            }`}
          >
            Contact
          </a>
        </nav>

        {/* Right Tools */}
        <div className="hidden sm:flex items-center gap-4">
          <select
            aria-label="language"
            className="bg-transparent border rounded px-2 py-1 text-sm text-gray-700"
            value={language}
            onChange={(e) => setLanguage(e.target.value)}
          >
            <option>English</option>
            <option>தமிழ்</option>
            <option>සිංහල</option>
          </select>
          <button
            className="uppercase text-white font-semibold bg-orange-500 px-3 py-1 rounded-2xl hover:bg-orange-700"
            aria-label="login"
          >
            Login
          </button>
        </div>

        {/* Mobile Toggle */}
        <button
          onClick={() => setMenuOpen(!menuOpen)}
          className="sm:hidden text-2xl text-gray-700"
          aria-label="Toggle Menu"
        >
          {menuOpen ? <FaTimes /> : <FaBars />}
        </button>
      </div>

      {/* Mobile Menu */}
      <AnimatePresence>
        {menuOpen && (
          <motion.div
            initial={{ height: 0, opacity: 0 }}
            animate={{ height: 'auto', opacity: 1 }}
            exit={{ height: 0, opacity: 0 }}
            transition={{ duration: 0.3 }}
            className="sm:hidden px-4 py-4 space-y-4 text-center shadow-md overflow-hidden"
          >
            <a
              href="#home"
              onClick={() => setMenuOpen(false)}
              className={`block uppercase text-base font-semibold px-3 py-1 rounded-2xl transition-all ${
                activeSection === 'home'
                  ? 'bg-orange-500 text-white shadow-md'
                  : 'text-black hover:bg-orange-700 hover:text-white hover:shadow-md'
              }`}
            >
              Home
            </a>
            <a
              href="#about"
              onClick={() => setMenuOpen(false)}
              className={`block uppercase text-base font-semibold px-3 py-1 rounded-2xl transition-all ${
                activeSection === 'about'
                  ? 'bg-orange-500 text-white shadow-md'
                  : 'text-black hover:bg-orange-700 hover:text-white hover:shadow-md'
              }`}
            >
              About
            </a>

            {/* Service + Sublinks */}
            <div className="space-y-2">
              <a
                href="#service"
                onClick={() => setMenuOpen(false)}
                className={`block uppercase text-base font-semibold px-3 py-1 rounded-2xl transition-all ${
                  activeSection === 'service'
                    ? 'bg-orange-500 text-white shadow-md'
                    : 'text-black hover:bg-orange-700 hover:text-white hover:shadow-md'
                }`}
              >
                Service
              </a>
              <div className="pl-4 p-2 w-100">
                <a
                  href="#web"
                  onClick={() => setMenuOpen(false)}
                  className="block px-4 py-2 text-base text-black hover:bg-orange-700 hover:text-white rounded-full"
                >
                  Web Development
                </a>
                <a
                  href="#marketing"
                  onClick={() => setMenuOpen(false)}
                  className="block px-4 py-2 text-base text-black hover:bg-orange-700 hover:text-white rounded-full"
                >
                  Digital Marketing
                </a>
              </div>
            </div>

            <Link
              href="./components/product"
              onClick={() => setMenuOpen(false)}
              className="block uppercase text-base font-semibold px-3 py-1 rounded-2xl text-black hover:bg-orange-700 hover:text-white hover:shadow-md"
            >
              Product
            </Link>
            <a
              href="#contact"
              onClick={() => setMenuOpen(false)}
              className={`block uppercase text-base font-semibold px-3 py-1 rounded-2xl transition-all ${
                activeSection === 'contact'
                  ? 'bg-orange-500 text-white shadow-md'
                  : 'text-black hover:bg-orange-700 hover:text-white hover:shadow-md'
              }`}
            >
              Contact
            </a>

            {/* Language and login for mobile */}
            <div className="mt-4 space-y-2">
              <select
                aria-label="language"
                className="w-full border rounded px-2 py-1 text-sm"
                value={language}
                onChange={(e) => setLanguage(e.target.value)}
              >
                <option>English</option>
                <option>தமிழ்</option>
                <option>සිංහල</option>
              </select>
              <button className="w-full uppercase text-white font-semibold bg-orange-500 px-3 py-1 rounded-2xl hover:bg-orange-700">
                Login
              </button>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </motion.header>
  );
}
